import * as Yup from 'yup';
import PropTypes from 'prop-types';
import React from 'react';
import { useSnackbar } from 'notistack';
import { useNavigate } from 'react-router-dom';
import { Form, FormikProvider, useFormik } from 'formik';

// material
import { Save as SaveIcon } from '@mui/icons-material';
import { LoadingButton } from '@mui/lab';
import {
  Box,
  Card,
  Grid,
  Stack,
  Switch,
  TextField,
  Typography,
  FormControlLabel,
  FormLabel,
  MenuItem,
} from '@mui/material';

import { styled } from '@mui/material/styles';
// utils
// routes
import { PATH_DASHBOARD } from '../../routes/paths';

const SwitchCardStyle = styled(Card)(() => ({
  boxShadow: 'none',
  borderRadius: 8,
}));

const NewReportSchema = Yup.object().shape({
  name: Yup.string().required('Name is required'),
  location: Yup.string(),
  hide: Yup.boolean(),
});

ReportNewForm.propTypes = {
  isEdit: PropTypes.bool,
  currentReport: PropTypes.object,
};

export default function ReportNewForm({ isEdit, currentReport }) {
  const navigate = useNavigate();
  const { enqueueSnackbar } = useSnackbar();

  const formik = useFormik({
    enableReinitialize: true,
    initialValues: {
      name: currentReport?.name || '',
      location: currentReport?.location || '',
      hide: currentReport?.name || true,
    },
    validationSchema: NewReportSchema,
    onSubmit: async (values, { setSubmitting, resetForm, setErrors }) => {
      alert(JSON.stringify(values));
      try {
        resetForm();
        setSubmitting(false);
        enqueueSnackbar(!isEdit ? 'Create success' : 'Update success', { variant: 'success' });
        navigate(PATH_DASHBOARD.report.list);
      } catch (error) {
        console.error(error);
        setSubmitting(false);
        setErrors(error);
      }
    },
  });

  const { errors, values, touched, handleSubmit, isSubmitting, getFieldProps } = formik;

  console.log('*** ', values, errors);

  return (
    <FormikProvider value={formik}>
      <Form noValidate autoComplete="off" onSubmit={handleSubmit}>
        <Grid container spacing={3}>
          <Grid item md={12}>
            <Card sx={{ p: 3 }}>
              <Stack spacing={3}>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ sm: 2 }}>
                  <TextField
                    fullWidth
                    size="small"
                    label="Name"
                    {...getFieldProps('name')}
                    error={Boolean(touched.name && errors.name)}
                    helperText={touched.name && errors.name}
                  />
                </Stack>
                <Stack direction={{ xs: 'column', sm: 'row' }} spacing={{ sm: 2 }}>
                  <TextField
                    select
                    fullWidth
                    size="small"
                    label="Location"
                    placeholder="Location"
                    {...getFieldProps('location')}
                    error={Boolean(touched.location && errors.location)}
                    helperText={touched.location && errors.location}
                  >
                    {[
                      { label: 'Content', value: 'content' },
                      { label: 'Profile', value: 'profile' },
                      { label: 'Task', value: 'task' },
                    ].map((option) => (
                      <MenuItem key={option.value} value={option.label}>
                        {option.label}
                      </MenuItem>
                    ))}
                  </TextField>
                </Stack>
                <SwitchCardStyle sx={{ py: 0.1, px: 2 }} variant="outlined">
                  <Stack
                    direction={{ xs: 'column', sm: 'row' }}
                    spacing={{ sm: 2 }}
                    justifyContent="space-between"
                    alignItems="center"
                  >
                    <FormLabel component="legend">Hide reports immediately</FormLabel>
                    <FormControlLabel
                      labelPlacement="start"
                      control={<Switch {...getFieldProps('hide')} checked={values.hide} />}
                      label={
                        <Typography variant="subtitle2" sx={{ mb: 0.5 }}>
                          Yes
                        </Typography>
                      }
                      sx={{ mx: 0, width: 1, justifyContent: 'end' }}
                    />
                  </Stack>
                </SwitchCardStyle>

                <Box sx={{ mt: 3, display: 'flex', justifyContent: 'flex-end' }}>
                  <LoadingButton type="submit" variant="contained" loading={isSubmitting} endIcon={<SaveIcon />}>
                    Save
                  </LoadingButton>
                </Box>
              </Stack>
            </Card>
          </Grid>
        </Grid>
      </Form>
    </FormikProvider>
  );
}
